.returnTop {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  right: 10px;
  bottom: 10px;
  cursor: pointer;
  -webkit-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.returnTop.returnTop_active {
  visibility: visible;
  opacity: 1;
}
.returnTop i {
  font-size: 40px;
  color: #508de8;
}



