.product_wrap {
  margin: 50px 0;
}
.product_wrap .product_wrap_content {
  width: 1200px;
  margin: 0 auto;
}
.product_wrap .product_wrap_content .product_title {
  text-align: center;
  margin: 80px 0;
}
.product_wrap .product_wrap_content .product_title h2 {
  color: #333333;
  font-size: 30px;
  font-weight: normal;
}
.product_wrap .product_wrap_content .product_title p {
  color: #666666;
  margin-top: 10px;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item {
  float: left;
  width: 240px;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_top {
  width: 240px;
  text-align: center;
  cursor: pointer;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_top:hover div {
  background-position: 60px 0;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_top:hover p {
  color: #387ee8;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_top div {
  width: 60px;
  height: 60px;
  margin: 0 auto;
  background-position: 0 0;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_top p {
  color: #666666;
  margin: 10px 0;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_top i {
  display: block;
  margin-left: 104px;
  width: 32px;
  height: 13px;
  background-image: url('../img/arrow.png');
  opacity: 0;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_top i.product_arrow_active {
  opacity: 1;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_bottom {
  position: relative;
  left: -100%;
  display: none;
  width: 1200px;
  padding: 20px;
  background-color: #f7f8fa;
  z-index: 1;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_bottom.product_bottom_active {
  display: block;
  z-index: 5;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_bottom .product_bottom_detail {
  float: left;
  width: 280px;
  height: 120px;
  margin: 10px;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_bottom .product_bottom_detail:nth-of-type(4n+1) {
  clear: both;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_bottom .product_bottom_detail:hover {
  -webkit-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  background-color: #fff;
  -webkit-box-shadow: 0 0 10px #dfdfdf;
          box-shadow: 0 0 10px #dfdfdf;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_bottom .product_bottom_detail a {
  display: block;
  padding: 20px;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_bottom .product_bottom_detail a h3 {
  font-weight: normal;
  color: #333333;
}
.product_wrap .product_wrap_content .product_content .product_content_line .product_line_item .product_item_bottom .product_bottom_detail a p {
  font-size: 14px;
  color: #999999;
  margin-top: 15px;
}



