.gift_wrap {
  position: relative;
  height: 240px;
  min-width: 1200px;
  overflow: hidden;
  margin: 0 auto;
}
.gift_wrap .gift_wrap_content {
  position: absolute;
  width: 1200px;
  left: 50%;
  margin-left: -600px;
  z-index: 5;
}
.gift_wrap .gift_wrap_content .gift_content_text {
  width: 600px;
  height: 240px;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 70px;
  text-align: center;
}
.gift_wrap .gift_wrap_content .gift_content_text h2 {
  font-size: 25px;
  font-weight: normal;
  color: #fff;
}
.gift_wrap .gift_wrap_content .gift_content_text div {
  width: 140px;
  margin-left: 240px;
  margin-top: 30px;
  height: 35px;
  border: 1px solid #fff;
  border-radius: 3px;
  text-align: center;
  line-height: 35px;
  -webkit-transition: background-color 0.2s ease-in-out;
  -o-transition: background-color 0.2s ease-in-out;
  transition: background-color 0.2s ease-in-out;
}
.gift_wrap .gift_wrap_content .gift_content_text div:hover {
  background-color: #fff;
}
.gift_wrap .gift_wrap_content .gift_content_text div:hover > a {
  color: #387ee8;
}
.gift_wrap .gift_wrap_content .gift_content_text div a {
  display: block;
  color: #fff;
  -webkit-transition: color 0.2s ease-in-out;
  -o-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
}
.gift_wrap .gift_content_video {
  position: absolute;
  left: 50%;
  margin-left: -960px;
}



