.solution_wrap {
  max-width: 1920px;
  height: 740px;
  margin: 0 auto;
  background-image: url('../img/05.jpg');
  background-color: #040227;
}
.solution_wrap .solution_wrap_content {
  width: 1200px;
  margin: 0 auto;
}
.solution_wrap .solution_wrap_content .solution_content_left {
  float: left;
  width: 280px;
  height: 740px;
  background-image: url('../img/solution_02.png');
}
.solution_wrap .solution_wrap_content .solution_content_left .solution_left_nav {
  margin-top: 170px;
}
.solution_wrap .solution_wrap_content .solution_content_left .solution_left_nav .solution_nav_item {
  height: 100px;
  font-size: 18px;
  color: #fff;
  line-height: 100px;
  cursor: pointer;
}
.solution_wrap .solution_wrap_content .solution_content_left .solution_left_nav .solution_nav_item img {
  width: 30px;
  vertical-align: middle;
  margin-left: 80px;
  margin-right: 10px;
}
.solution_wrap .solution_wrap_content .solution_content_left .solution_left_nav .solution_nav_item span {
  vertical-align: middle;
}
.solution_wrap .solution_wrap_content .solution_content_left .solution_left_nav .solution_nav_item.solution_nav_active {
  border-left: 2px solid #387ee8;
  background-image: url('../img/solution_01.png');
}
.solution_wrap .solution_wrap_content .solution_content_right {
  float: right;
  width: 920px;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_title {
  width: 500px;
  height: 200px;
  text-align: center;
  margin-left: 80px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 50px;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_title h2 {
  font-size: 25px;
  font-weight: normal;
  color: #fff;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_title p {
  color: #d9d9d9;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel {
  position: relative;
  margin-left: 80px;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item {
  position: absolute;
  opacity: 0;
  z-index: 1;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item.solution_panel_active {
  -webkit-transition: opacity 1s ease-in-out;
  -o-transition: opacity 1s ease-in-out;
  transition: opacity 1s ease-in-out;
  opacity: 1;
  z-index: 5;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_text {
  height: 100px;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_text h3 {
  font-size: 20px;
  color: #fff;
  margin-bottom: 10px;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_text p {
  color: #d9d9d9;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_button {
  width: 120px;
  height: 35px;
  margin: 10px 0;
  border: 1px solid #fff;
  border-radius: 3px;
  text-align: center;
  line-height: 35px;
  -webkit-transition: background-color 0.5s ease-in-out;
  -o-transition: background-color 0.5s ease-in-out;
  transition: background-color 0.5s ease-in-out;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_button:hover {
  background-color: #fff;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_button:hover > a {
  color: black;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_button a {
  color: #fff;
  display: block;
  -webkit-transition: color 0.5s ease-in-out;
  -o-transition: color 0.5s ease-in-out;
  transition: color 0.5s ease-in-out;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_client {
  margin: 50px 0;
  border-top: 1px solid #65666b;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_client h3 {
  color: #fff;
  font-weight: normal;
  margin: 30px 0;
}
.solution_wrap .solution_wrap_content .solution_content_right .solution_right_panel .solution_panel_item .solution_item_client img {
  width: 200px;
  margin-left: -40px;
}



