.loginFooter_wrap {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #242a37;
}
.loginFooter_wrap .loginFooter_wrap_content {
  width: 1200px;
  margin: 0 auto;
}
.loginFooter_wrap .loginFooter_wrap_content .loginFooter_info {
  height: 50px;
  text-align: center;
  line-height: 50px;
  border-top: 1px solid #3b3f42;
}
.loginFooter_wrap .loginFooter_wrap_content .loginFooter_info p {
  color: #999999;
  font-size: 14px;
}



