.loginHeader {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  background-color: #242a37;
}
.loginHeader .loginHeader_wrap {
  width: 70%;
  min-width: 1200px;
  margin: 0 auto;
}
.loginHeader .loginHeader_wrap .loginHeader_left {
  float: left;
  width: 170px;
}
.loginHeader .loginHeader_wrap .loginHeader_left a {
  display: block;
}
.loginHeader .loginHeader_wrap .loginHeader_left a img {
  height: 60px;
  vertical-align: middle;
}
.loginHeader .loginHeader_wrap .loginHeader_left a span {
  color: #fff;
  margin-left: 10px;
  vertical-align: middle;
}



