.footer_wrap {
  max-width: 1920px;
  margin: 0 auto;
  background-color: #242a37;
}
.footer_wrap .footer_wrap_content {
  width: 1200px;
  margin: 0 auto;
}
.footer_wrap .footer_wrap_content .footer_serve {
  padding: 20px 0;
  border-bottom: 1px solid #3b3f42;
}
.footer_wrap .footer_wrap_content .footer_serve .footer_serve_item {
  float: left;
  width: 33.33%;
  color: #fff;
  text-align: center;
}
.footer_wrap .footer_wrap_content .footer_serve .footer_serve_item img {
  width: 40px;
  margin-bottom: 10px;
  vertical-align: middle;
}
.footer_wrap .footer_wrap_content .footer_link {
  padding: 20px 0;
}
.footer_wrap .footer_wrap_content .footer_link .footer_link_item {
  float: left;
  width: 25%;
}
.footer_wrap .footer_wrap_content .footer_link .footer_link_item dl {
  width: 100px;
  margin-left: 100px;
}
.footer_wrap .footer_wrap_content .footer_link .footer_link_item dl dt {
  padding: 10px 0;
  color: #fff;
}
.footer_wrap .footer_wrap_content .footer_link .footer_link_item dl dd {
  padding: 4px 0;
}
.footer_wrap .footer_wrap_content .footer_link .footer_link_item dl dd a {
  color: #999999;
  font-size: 14px;
}
.footer_wrap .footer_wrap_content .footer_link .footer_link_item dl dd a:hover {
  color: #387ee8;
}
.footer_wrap .footer_wrap_content .footer_info {
  height: 50px;
  text-align: center;
  line-height: 50px;
  border-top: 1px solid #3b3f42;
}
.footer_wrap .footer_wrap_content .footer_info p {
  color: #999999;
  font-size: 14px;
}



