.header {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
}
.header .header_wrap {
  background-color: rgba(36, 42, 55, 0);
  -webkit-transition: background-color 0.2s ease-in-out 0.5s;
  -o-transition: background-color 0.2s ease-in-out 0.5s;
  transition: background-color 0.2s ease-in-out 0.5s;
}
.header .header_wrap:hover {
  background-color: #242a37;
  -webkit-transition: background-color 0.2s ease-in-out;
  -o-transition: background-color 0.2s ease-in-out;
  transition: background-color 0.2s ease-in-out;
}
.header .header_wrap.header_wrap_active {
  background-color: #242a37;
  -webkit-transition: background-color 0.5s ease-in-out;
  -o-transition: background-color 0.5s ease-in-out;
  transition: background-color 0.5s ease-in-out;
}
.header .header_wrap .header_wrap_content {
  width: 1300px;
  margin: 0 auto;
}
.header .header_wrap .header_wrap_content .header_left {
  float: left;
  width: 170px;
}
.header .header_wrap .header_wrap_content .header_left a {
  display: block;
}
.header .header_wrap .header_wrap_content .header_left a img {
  height: 60px;
  vertical-align: middle;
}
.header .header_wrap .header_wrap_content .header_left a span {
  color: #fff;
  margin-left: 10px;
  vertical-align: middle;
}
.header .header_wrap .header_wrap_content .header_middle nav ul li,
.header .header_wrap .header_wrap_content .header_right nav ul li {
  display: inline-block;
  text-align: center;
  height: 60px;
  line-height: 60px;
  padding: 0 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.header .header_wrap .header_wrap_content .header_middle nav ul li .header_nav_item,
.header .header_wrap .header_wrap_content .header_right nav ul li .header_nav_item {
  display: block;
  height: 60px;
  font-size: 14px;
  color: #fff;
}
.header .header_wrap .header_wrap_content .header_middle {
  float: left;
}
.header .header_wrap .header_wrap_content .header_middle li:hover {
  border-bottom: 2px solid #387ee8;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1):hover .header_middle_list {
  top: 60px;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list {
  position: absolute;
  width: 100%;
  left: 0;
  background-color: #1f2229;
  top: -1000px;
  -webkit-transition: top .5s linear;
  -o-transition: top .5s linear;
  transition: top .5s linear;
  z-index: -1;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_middle_product_total {
  width: 100%;
  height: 60px;
  background-color: #242730;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_middle_product_total .header_middle_product_wrap {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 70px;
  width: 1300px;
  height: 100%;
  margin: 0 auto;
  text-align: left;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_middle_product_total .header_middle_product_wrap span {
  font-size: 14px;
  margin-right: 20px;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_middle_product_total .header_middle_product_wrap span:nth-of-type(1) {
  color: #e2e2e2;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_middle_product_total .header_middle_product_wrap span a {
  font-size: 14px;
  color: #999999;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_middle_product_total .header_middle_product_wrap span a:hover {
  color: #387ee8;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_category {
  width: 1300px;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px 0 20px 70px;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_category .header_category_item {
  float: left;
  width: 20%;
  text-align: left;
  line-height: 30px;
  margin-bottom: 20px;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_category .header_category_item dl dt {
  width: 80%;
  height: 30px;
  color: #fff;
  font-size: 14px;
  border-bottom: 1px solid #36393f;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_category .header_category_item dl dd {
  height: 30px;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_category .header_category_item dl dd a {
  display: block;
  color: #999999;
  font-size: 14px;
}
.header .header_wrap .header_wrap_content .header_middle li:nth-of-type(1) .header_middle_list .header_category .header_category_item dl dd a:hover {
  color: #387ee8;
}
.header .header_wrap .header_wrap_content .header_right {
  float: right;
}
.header .header_wrap .header_wrap_content .header_right li:nth-of-type(1):hover > a,
.header .header_wrap .header_wrap_content .header_right li:nth-of-type(2):hover > a {
  color: #387ee8;
}
.header .header_wrap .header_wrap_content .header_right li:nth-of-type(3) {
  background-color: #387ee8;
}
.header .header_wrap .header_wrap_content .header_right li:nth-of-type(3):hover {
  background-color: #508de8;
}
.header .header_wrap .header_wrap_content .header_right li:nth-of-type(3):hover > a {
  color: #fff;
}
.header .header_wrap .header_wrap_content .header_right .hasLogin_wrap {
  float: right;
  width: 60px;
  height: 60px;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}
.header .header_wrap .header_wrap_content .header_right .hasLogin_wrap a {
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.header .header_wrap .header_wrap_content .header_right .hasLogin_wrap a img {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-left: 10px;
  border-radius: 50%;
  vertical-align: middle;
}
.header .header_wrap .header_wrap_content .header_right .hasLogin_wrap:hover > .userInfo_wrap {
  visibility: visible;
  opacity: 1;
}
.header .header_wrap .header_wrap_content .header_right .hasLogin_wrap .userInfo_wrap {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: .3s linear;
  -o-transition: .3s linear;
  transition: .3s linear;
  width: 150px;
  height: 100px;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #242a37;
  border-radius: 0 0 10px 10px ;
  margin-left: -45px;
  text-align: center;
}
.header .header_wrap .header_wrap_content .header_right .hasLogin_wrap .userInfo_wrap h3 {
  font-weight: normal;
}
.header .header_wrap .header_wrap_content .header_right .hasLogin_wrap .userInfo_wrap .cancle_login {
  font-weight: normal;
  margin-top: 20px;
  font-size: 14px;
  color: #c4c4c4;
  cursor: pointer;
}
.header .header_wrap .header_wrap_content .header_right .hasLogin_wrap .userInfo_wrap .cancle_login:hover {
  color: #508de8;
}



